import {
	uniPopup
} from "@/components/uni-popup/uni-popup.vue"

const recorderManager = uni.getRecorderManager();
const innerAudioContext = uni.createInnerAudioContext();

innerAudioContext.autoplay = true;
export default {
	components: {
		uniPopup,
	},
	data() {
		return {
			imagePath: '',
			bottom: true,
			active: false,
			voicePath: "",
		}
	},
	methods: {
		playVoice() {
			console.log('播放录音');
			console.log(this.voicePath)
			if (this.voicePath) {
				innerAudioContext.src = this.voicePath;
				innerAudioContext.play();
				console.log("play voice")
			}
		},
		start: function() {
			this.longTag = setTimeout(this.recording, 500); // 定义超过500ms即为长按，可自行修改
			console.log("start")
		},
		move: function() {
			// clearTimeout(this.longTag);
			// clearTimeout(this.maxTiming);
			// clearInterval(this.draw);
			// this.longTag = false;
			// console.log("move")
		},
		end: function() {
			console.log("end")
			recorderManager.stop();
			clearTimeout(this.longTag);
			clearTimeout(this.maxTiming);
			clearInterval(this.draw);
			if (this.longTag) { // timeout不是false证明没有触发recording或者touchmove事件
				console.log('点击事件');
			}
			this.longTag = false;
			this.draw = false;
			this.reset();
			// if (this.voicePath) {
			// 	innerAudioContext.src = this.voicePath;
			// 	innerAudioContext.play();
			// }
			// 语音转换
			// this.send();
		},
		recording: function() {
			let self = this;

			// 开始录音
			this.longTag = false;
			// recorderManager.start();
			console.log('录音开始');
			recorderManager.start();
			// 最大录音时间10秒
			this.maxTiming = setTimeout(function() {
				clearInterval(this.draw);
				console.log('时间到');

				// 语音转换
				// self.send();
			}, self.max);

			// 录音过程圆圈动画
			let angle = -0.5;
			let context = uni.createCanvasContext('canvas');
			this.draw = setInterval(function() {
				context.beginPath();
				context.setStrokeStyle("#1296db");
				context.setLineWidth(3);
				context.arc(50, 50, 25, -0.5 * Math.PI, (angle += 2 / (self.max / self.frame)) * Math.PI, false);
				context.stroke();
				context.draw();
			}, self.frame);
		},
		send: function() {
			// recorderManager.stop();
			uni.showModal({
				title: '提示',
				content: '是否进行语音识别',
				confirmColor: '#1296DB',
				success: function(res) {
					if (res.confirm) {
						console.log("确认")
					} else if (res.cancel) {
						console.log("取消")
					}
				}
			});
		},
		timing: function() {
			let self = this;

		},
		reset: function() {
			this.seconds = '00';
			this.ms = '00';
		},
		saveRecord: function(recordPath) {
			console.log("===音频文件地址：" + recordPath + "===")
			//do... 可以使用 uni.uploadFile 接口上传到服务器
		},
		togglePopup(item) {
			this.bottom = true;
		},
		hidePopup() {
			console.log("hide")
			this.bottom = false;
		},
		takePhoto() {
			uni.chooseImage({
				count: 1,
				success: res => {
					this.imagePath = res.tempFilePaths[0];
					console.log(this.imagePath)
					// this.getTempFilePath(res.tempFilePaths[0],'imageTempPath');
					uni.previewImage({
						urls: res.tempFilePaths,
						success(res) {
							console.log(res)
						}
					});
				}
			});
		},
	}
}
